* {
  margin: 0;
  padding: 0;
}
form {
  border: 3px solid #b0adad;
  border-radius: 15px;
  box-shadow: 10px 10px 11px 6px rgba(0, 0, 0, 0.3);
}
.main-sec {
  margin-left: auto;
  margin-right: auto;
  border-radius: 15px;
  width: 300px;
  height: 400px;
  margin-top: 40px;
  margin-bottom: 40px;
}
.reg-button {
  margin-top: 20px;
  background-color: blue;
  text-align: center;
  margin-bottom: 20px;
}
.user {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.link-reg {
  display: flex;
  justify-content: right;
  margin-right: 5px;
  padding-bottom: 5px;
}
input[type="text"],
input[type="password"] {
  width: 98%;
  padding: 12px 10px;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
  border-radius: 10px;
  margin: 5px 5px 5px 5px;
  box-shadow: 0px 16px 16px 0px rgba(0, 0, 0, 0.2);
}
